      pointer-events: none;
      bottom: 5%;
      right: 0;
      width: 50%;
      height: 100px;
      opacity: 0;
      transition: opacity 0.15s ease;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center top;
   }

   /* Hover or Focus Animation */
   .link-in:hover::after,
   .link-in:focus::after {
