      padding: 1rem 2rem;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      border: 1px solid var(--text);
   }

   .option h4 {
      line-height: 3rem;
      border-bottom: 1px solid var(--text);
      padding-left: 0.5rem;
   }

   .option h4:nth-child(n + 2) {
      margin-top: 4rem;
   }

   .option ol {
      width: 100%;
      height: fit-content;
      margin: 0 0.4rem;
   }

   .option ol div {
      font-size: clamp(2rem, 1.543rem + 2.29vw, 2.4rem);
      font-weight: 500;
      height: 320px;
      margin-top: 0.5rem;
      padding: 3rem 1rem;
      border: 1px solid var(--text);
      overflow-x: auto;
   }

   .option ol li {
      font-size: clamp(1.2rem, 0.743rem + 2.29vw, 1.6rem);
      width: fit-content;
      margin-top: 0.5rem;
      letter-spacing: 0.015rem;
   }

